/**
 * OscilloscopeState.js (ESM)
 *  - 
 */
export const OscilloscopeState = (function() {
  // 
  const state = {
    // 
    displayAdjustFactors: {
      time: 1.0,
      volts: { 1: 1.0, 2: 1.0 }
    },

    // 
    signal: {
      type: 'sine',
      frequencies: { 1: 1, 2: 1 },
      peakValues: { 1: 1, 2: 1 },
      phase: 0,
      phaseDiff: 0
    },

    // 
    trigger: {
      level: 0,
      mode: 'auto',
      slope: 'rising',
      source: 1,
      active: true
    },

    // 
    viewport: {
      timeDiv: 1,
      voltsDiv: { 1: 1, 2: 1 },
      horizontalPosition: 0,
      verticalPosition: { 1: 0, 2: 0 }
    },

    // 
    calibration: {
      factor: 1.0,
      saved: {
        displayAdjustFactors: {
          time: 1.0,
          volts: { 1: 1.0, 2: 1.0 }
        },
        factor: 1.0
      }
    },

    // 
    channels: {
      1: { active: false },
      2: { active: false }
    },

    // 
    displayMode: 'independent',

    // 
    expStep: 'calibration'
  };

  // 
  const listeners = new Set();

  // 
  function notifyListeners() {
    listeners.forEach(listener => listener(state));
  }

  return {
    // 
    getState() {
      return { ...state };
    },

    // 
    get(path) {
      return path.split('.').reduce((obj, key) => obj?.[key], state);
    },

    // 
    update(path, value) {
      const keys = path.split('.');
      const lastKey = keys.pop();
      const target = keys.reduce((obj, key) => obj[key], state);
      target[lastKey] = value;
      notifyListeners();
    },

    // 
    batchUpdate(updates) {
      Object.entries(updates).forEach(([path, value]) => {
        this.update(path, value);
      });
    },

    // 
    subscribe(listener) {
      listeners.add(listener);
      return () => listeners.delete(listener);
    },

    // 
    reset() {
      Object.assign(state, {
        displayAdjustFactors: {
          time: 1.0,
          volts: { 1: 1.0, 2: 1.0 }
        },
        signal: {
          type: 'sine',
          frequencies: { 1: 1, 2: 1 },
          peakValues: { 1: 1, 2: 1 },
          phase: 0,
          phaseDiff: 0
        },
        trigger: {
          level: 0,
          mode: 'auto',
          slope: 'rising',
          source: 1,
          active: true
        },
        viewport: {
          timeDiv: 1,
          voltsDiv: { 1: 1, 2: 1 },
          horizontalPosition: 0,
          verticalPosition: { 1: 0, 2: 0 }
        }
      });
      notifyListeners();
    },

    // 
    export() {
      return JSON.stringify(state);
    },

    // 
    import(jsonState) {
      try {
        const newState = JSON.parse(jsonState);
        Object.assign(state, newState);
        notifyListeners();
      } catch (error) {
        console.error(':', error);
      }
    }
  };
})();

export default OscilloscopeState;